.map-container {
  height: 400px;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.301);
  &:active {
    border: none;
  }
}
.mapboxgl-ctrl-logo {
  display: none !important;
}

.mapboxgl-ctrl-bottom-right {
  display: none;
}
